#skills{
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 5rem;
}
.skills{
  width: 60%;
}
.skills_card{
  padding: 1rem 2rem;
  flex-wrap: wrap;
}
.skill_type{
  margin-bottom: 3rem;
  display: flex;
  align-items: center;
}

.skill_tittle{
  width: 10rem;
}

.skill_type ul{
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.skill_items{
  width: 6rem;
  height: 6rem;
  display: flex;
  border-radius: 1rem;
  align-items: center;
  justify-content: center;
  background: #2E2E48;
}
.skill_item{
  text-align: center;
}

.skill_icon{
  font-size: 3rem;
}

.tools{
  width: 60%;
}
.tool_card{
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.tool_item{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 1rem;
  height: 10rem;
  width: 20rem;
  background: #2E2E48;
  gap: 1rem;
}
.tool_icon{
  font-size: 3rem;
}

@media (max-width: 1600px) {
  #skills {
    padding-top: 3rem;
  }
  .skills{
    width: 78%;
  }
  .skill_icon{
    font-size: 2rem;
  }
  .skill_items{
    width:6rem;
    height: 6em;
  }
  .skill_item p{
    font-size: .7rem ;
  }
  .skill_tittle{
    width: 8rem;
  }
  .skill_tittle p{
    font-size: .8rem;
  }
  .tools{
    width: 78%;
  }
  .tool_item{
    height: 7rem;
    width: 10rem;
  }
  .tool_item p{
    font-size: .8rem ;
  }
  .tool_icon{
    font-size: 2rem;
  }
}

@media (max-width: 1024px) {
  .skills{
    width: 85%;
  }
  .skill_icon{
    font-size: 1.5rem;
  }
  .skill_items{
    width:6rem;
    height: 6em;
  }
  .skill_item p{
    font-size: .7rem ;
  }
  .skill_tittle{
    width: 8rem;
  }
  .skill_tittle p{
    font-size: .6rem;
  }
  .tools{
    width: 85%;
  }
  .tool_item{
    height: 7rem;
    width: 10rem;
  }
  .tool_item p{
    font-size: .6rem ;
  }
  .tool_icon{
    font-size: 1.5rem;
  }
}


@media (max-width: 768px) {
  .skills{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 90%;
  }

  .skill_type{
    display: flex;
    flex-direction: column;
    /* padding: 0; */
  }
  .skill_type ul{
    display: grid;
    grid-template-columns: repeat(4, 5rem);
    justify-content: center;
    gap: .8rem;
  }
  .skill_icon{
    font-size: 2rem;
  }
  .skill_items{
    width:5rem;
    height: 5em;
  }
  .skill_item p{
    font-size: .7rem ;
  }
  .skill_tittle{
    font-size: 1.7rem;
    display: flex;
    justify-content: center;
    text-align: center;
    gap: 1rem;
    margin-bottom: 1rem;
  }
  
  .skill_tittle p{
    font-size: .6rem;
  }
  .icon_skill{
    font-size: 1rem;
  }


  .tools{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 90%;
  }
  .tool_card{
    width: 100%;
    /* background: #000; */
    display: flex;
    justify-content: space-between;
  }
  .tool_item{
    height: 4rem;
    width: 5rem;
  }
  .tool_item p{
    font-size: .4rem ;
  }
  .tool_icon{
    font-size: 1.5rem;
  }
}