header {
  text-align: center;
  height: 100vh;
  padding-top: 5rem;
  overflow: hidden;
}
.name {
  font-size: 3rem;
  text-transform: uppercase;
}

.social {
  padding: 1rem 0;
}
.media {
  /* background-color: #f44336; */
  padding: 1rem;
  font-size: 2rem;
  color: var(--color-light);
}
.media:hover {
  color: var(--color-primary-variant);
}
.media:visited {
  /* color: red; */
  color: var(--color-primary-variant);
}
.me {
  width: 30rem;
  height: 100%;
  margin: 2rem auto;
}
.me img {
  width: 100%;
}
@media screen and (max-width: 1024px) {
  .social{
    margin: 3rem 0;
  }
  .media{
    padding: .9rem;
    font-size: 1.5rem;
  }
  .me {
    width: 100%;
  }
}
