#about {
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 5rem;
}
.about_container {
  margin-top: 6rem;
  display: grid;
  grid-template-columns: repeat(2, 35% 60%);
  gap: 2rem;
  width: 60%;
}
.about_me {
  display: flex;
  text-align: center;
}
.about_img {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1rem;
}
.about_img img {
  width: 80%;
}
.about_content {
  padding-top: 1rem;
}
.phrase {
  margin-top: 3rem;
  text-align: end;
}

@media (max-width: 1500px) {
  .about_container {
    grid-template-columns: repeat(2, 45% 60%);
  }
  .about_me img {
    height: auto;
    border-radius: 1rem;
  }
}
@media (max-width: 1024px) {
  #about {
    padding-top: 3rem;
  }
  .about_container {
    grid-template-columns: repeat(1, 100%);
    justify-content: center;
    margin-top: 2rem;
  }
  .about_me {
    display: flex;
    justify-content: center;
    width: 100%;
  }
  .about_me .about_img {
    width: 40%;
  }
  .about_content {
    font-size: 1rem;
  }
}


@media (max-width: 768px) {
  #about {
    padding-top: 4rem;
  }
  .about_container {
    grid-template-columns: repeat(1, 100%);
    justify-content: center;
    margin-top: 2rem;
  }
  .about_me .about_img {
    width: 80%;
  }
	.about_content {
		padding-top: 1rem;
	}
	.phrase {
		margin-top: 1.5rem;
		text-align: end;
	}
  .about_content {
    font-size: .8rem;
  }

}

